<script>
	import mpopup from '../../components/xuan-popup/xuan-popup.vue'
	export default {
		data() {
			return {
				proid:[],//购物车中的商品id集合
				kkk:[],//拿到了该商品对象
				// ////
				autoplay:"true",//自动播放
							startX: 0, //触摸开始位置
							moveX: 0, //移动的位置
							elementW: 0, //元素的宽度
							transLeft: 0, //位移距离
							boxWidth: 9999, //盒子的宽度
							Trans: false, //滑动效果
							current: 0, //默认下标
							timer: null, //定时器
							frequency: 0, //次数
							list: [
								// {path:'../../static/myimg/1.jpg',url : ''},
								// {path:'../../static/myimg/2.jpg',url : ''},
								// {path:'../../static/myimg/3.jpg',url : ''},
								// {path:'../../static/myimg/4.jpg',url : ''},
								// {path:'../../static/myimg/5.jpg',url : ''},
								// {path:'../../static/myimg/6.jpg',url : ''}
							],//轮播数据
							banner:[
								{path:'../../static/myimg/1.jpg',url : ''},
								{path:'../../static/myimg/2.jpg',url : ''},
								{path:'../../static/myimg/3.jpg',url : ''},
								{path:'../../static/myimg/4.jpg',url : ''},
								{path:'../../static/myimg/5.jpg',url : ''},
								{path:'../../static/myimg/6.jpg',url : ''}
							], //轮播数据
				///////
			}
		},
		components:{
			mpopup
		},
		methods: {
			
		},
/////////////////////////////////////////////////////	
	onLoad(options) {
			var thar=this
			//options可以接到index 传过来的值
			console.log("张飞",options);
			this.proid.push({"id":options.id,"num":1})
			console.log("商品id数组",this.proid)//测试正常,正常运作
			uni.request({
						url: 'http://127.0.0.1/mypro/', //仅为示例，并非真实接口地址。
						method: 'POST',
						data: {
							id:options.id
						},
						dataType: 'json',
						success: res => {
							//console.log(res.data);
							this.kkk=res.data
							console.log("kkk",this.kkk)
							console.log(this.kkk[0].small_image)
							let img="/static/"+this.kkk[0].small_image
							thar.list.push({path:img,url : ''})
							thar.list.push({path:img,url : ''})
							thar.list.push({path:img,url : ''})
							thar.list.push({path:img,url : ''})
						}
					});
		},
	///////
	props: {
		 //下标
		index: {
			type: [String, Number],
			default: 1
		},

		/* 播放时间 */
		time: {
			type: [String, Number],
			default: 3000
		},
		/* 轮播高度 */
		height: {
			type: [String, Number],
			default: 690
		},
		/* 是否显示圈圈 */
		indicatorDots: {
			type: Boolean,
			default: true
		}
	},
	mounted() {
		this.setConfig();
	},
	created() {
		console.log(12144);
		
	},
	watch: {
		banner(val) {
			if (this.frequency < 1 && this.list.length == 0) {
				this.frequency++;
				this.hanldData();
				this.setConfig();
			}
		}
	},	
	////
methods:{
	pop:function(){
	            this.$refs.mpopup.open({
	                type:'success',
	                content:'加入购物车',
	                timeout:2000,
	            });
	        },
	// 加入购物车事件函数
showAttr(k){
	if(k==1){//如果是加入购物车行为//
		//必须先取出购物车本地数据,加上新加的,再写入本地,否则,每次新加会覆盖之前的所有购物车数据
		var mydata=[]
		mydata=uni.getStorageSync('myproid')//用同步,取出数组
		//进行判断,如本地购物车中已有该商品对象,则只用在数量上加1,
		//console.log(this.proid[0].id)
		console.log(2222222,mydata)
		if(mydata==""){
			mydata=[]
			console.log(2222222,mydata)
			uni.setStorageSync('myproid',[])
		}
		
		
		
		//先获取本地购物车中所有商品的id,形成数组
		let myzwl=[]
		for(var i of mydata){//不能用in
			myzwl.push(i.id)
		}
		console.log("myzwl",myzwl)//myzwl (3) ["6", "7", "8"] 正常取到了
		
		
		//再判断,本地购物车中是否已有该 商品对象 用id来比对
		var a = myzwl.indexOf(this.proid[0].id)
		//a 是元素在数组中的位置,如果没与搜索到则返回 -1 ,有,则为位置(如2)
		var that=this//因为层级过多,取不到this了,把this存在全局变量中,不管层级多深都能得到了
		console.log("跟踪mydata.push",mydata)
		if(a==-1){
			//本地购物车不包含该商品对象,直接存进去
			mydata.push(that.proid[0])//因为是数组,而且每次都只会是1个对象,不取[0],会存成多级数组
		}else{//已经存在了
			mydata.forEach(function(ele,index,myarray){//迭代本地购物车
				if(ele.id==that.proid[0].id){//找出该商品对象,只在数量上加1
					ele.num=parseInt(ele.num)+1//数字是以字符串保存,先变为数字
				}
				
			})
			
		}
		
		// 　　//forEach 第一个参数，放函数(ele,当前元素，index，当前索引位，self，就是arr本身)，
		// //第二个参数，可选，是第一个参数函数的this，不填默认window；
		
		// mydata.push(this.proid[0])//因为是数组,而且每次都只会是1个对象,不取[0],会存成多级数组
		// console.log("mydata",mydata)
								
		uni.setStorage({
						key:'myproid',
						data:mydata,
						success() {
							console.log('已存入购物车')
							// uni.showToast({
							// 				title: '已加入购物车',
							// 				duration: 1000
							// 			});
							that.pop()
						},
						fail() {
							console.log('存入购物车失败了')
						}
					}); 
	}
},///

	
	
	
	
	
	
	
	
	
	
	
	
//返回到mall.vue
imgclick() {
	uni.switchTab({
		url: '/pages/mall/mall'
	});
},////////////////////
/* 处理轮播数据 这里用的是最基本的轮播  头尾各加一张的写法 */
		hanldData() {
			let banner = JSON.parse(JSON.stringify(this.banner));
			let leng = banner.length - 1;
			let homeBanner = banner[0],
				lastBanner = banner[leng];
			banner.unshift(lastBanner);
			banner.push(homeBanner);
			this.list = banner;
		},
		/* 获取对应相关数据 */
		setConfig() {
			const that = this;
			if (that.banner.length != 0) {
				if (this.list.length == 0) {
					this.hanldData();
					this.setConfig();
				} else {
					/* 获取下标 */
					that.current = that.index || 1;
					/* 计算banner宽度 */
					this.elementW = uni.upx2px(690);
					that.Trans = false;
					that.transLeft = that.elementW * that.current;
					that.boxWidth = that.list.length * that.elementW;
					/* 添加过度效果 */
					let timer = null;
					clearTimeout(timer);
					timer = setTimeout(function() {
						that.Trans = true;
						/* 是否开启自动轮播 */
						if (that.autoplay) {
							that.auto();
						}
					}, 300);
				}
			}
		},
		/* 开始触摸 */
		touchstart(e) {
			const touches = e.touches[0];
			let clientX = touches.clientX,
				clientY = touches.clientY;
			this.startX = clientX;
			clearInterval(this.timer);
		},
		/* 触摸移动 */
		touchmove(e) {
			const touches = e.touches[0];
			let clientX = touches.clientX,
				clientY = touches.clientY;
			this.moveX = clientX;
		},
		/* 触摸结束 */
		touchend(e) {
			if (this.moveX - this.startX > 0) {
				//向右滑动
				this.sliderRight();
			} else {
				//向左滑动
				this.sliderLeft();
			}
			this.changeCurrent();
			if (this.autoplay) {
				this.auto();
			}
		},
		changeCurrent() {
			this.$emit('change', this.current);
		},
		/* 向左滑动 */
		sliderLeft() {
			const that = this;
			this.current++;
			this.Trans = true;
			if (this.current >= this.list.length - 1) {
				this.Trans = false;
				this.current = 0;
				setTimeout(() => {
					this.current++;
					this.Trans = true;
				}, 30);
			}
		},
		/* 向右滑动 */
		sliderRight() {
			const that = this;
			this.current--;
			this.Trans = true;
			if (this.current <= 0) {
				this.Trans = false;
				this.current = this.list.length - 1;
				setTimeout(() => {
					that.Trans = true;
					that.current--;
				}, 30);
			}
		},
		/* 自动播放 */
		auto() {
			const that = this;
			that.timer = null;
			clearInterval(that.timer);
			that.timer = setInterval(() => {
				that.sliderLeft();
			}, that.time);
		},
		/* 图片点击事件 */
		imgClick(){
			/* 逻辑操作 也可以暴露出去 */
			console.log('点击了图片');
		}



///////////////		
}
	}
</script>

